require('jquery-mask-plugin');
require('perfect-scrollbar/jquery')($);
require('jquery-ui/ui/widgets/datepicker');
require('./fullcalendar.js');

$('.js-scrollbar').perfectScrollbar({
  swipePropagation: false
});


$('.js-player-mask').mask('AXXXXXXXXXXXXXXXXXXXXXXXXXXXX/AYYYYYYYYYYYYYYYYYYYYYYYYYY/ZZ', {
  translation: {
    'X': {
      pattern: /[a-zA-Z-.]/,
      optional: true
    },
    'Y': {
      pattern: /[a-zA-Z0-9-.]/,
      optional: true
    },
    'Z': {
      pattern: /[0-9]/,
      optional: true
    }
  }
});

$('.js-datepicker').datepicker({
  onSelect: (date) => {
    $('.js-datepicker-date').text(date)
  }
});




