(function() {
  const $toggle = $('.js-toggle-subnav');
  const subnav = '.subnav';
  function bindEvents() {
    $toggle.on('click', toggleSubnav);
  }
  function toggleSubnav() {
    if ($(this).parent().hasClass('is-active')) {
      $(this).parent().removeClass('is-active').find(subnav).slideUp(200);
    }
    else {
      $toggle.parent().removeClass('is-active').find(subnav).slideUp(200);
      $(this).parent().addClass('is-active').find(subnav).slideDown(200);
    }
  }
  bindEvents();
})();


